body {
  margin: 0;
}

.people {
  grid-template-columns: repeat(2, 200px);
  justify-content: center;
  gap: 40px 30px;
  min-width: 500px;
  padding: 30px 35px;
  display: grid;
}

@media (width >= 1000px) {
  .people {
    grid-template-columns: repeat(4, 200px);
  }
}

.people__item {
  width: 224px;
  height: 224px;
}

.person {
  box-sizing: border-box;
  transform-origin: 50% 110px;
  padding: 12px 12px 19px;
  font-family: Arial, Helvetica, sans-serif;
  transition: transform 1s;
  position: relative;
}

.person:hover {
  z-index: 10;
  background-color: #eee;
  border-radius: 10px;
  transform: scale(1.2);
}

.person__photo {
  display: flex;
  position: relative;
}

.person__image {
  border-radius: 50%;
  width: 100%;
  transition: border-radius 1s;
}

.person:hover .person__image {
  border-radius: 25%;
}

.person__rank {
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-self: center;
  align-items: center;
  width: 32px;
  height: 32px;
  display: flex;
  position: absolute;
  right: 10px;
}

.person__details {
  flex-direction: column;
  justify-content: center;
  display: flex;
}

.person__name {
  text-align: center;
  color: #2f4f4f;
  margin-top: 8px;
  font-size: 20px;
  font-weight: 700;
  line-height: 100%;
  text-decoration: none;
}

.person__extra {
  text-align: center;
  color: #000;
  opacity: 0;
  margin-top: 9px;
  font-size: 20px;
  font-weight: 400;
  line-height: 100%;
  transition: opacity .5s;
}

.person:hover .person__extra {
  opacity: 1;
}

.person__sport:after {
  content: ", ";
}

.person__pay:before {
  content: "$";
}

.person__pay:after {
  content: "M";
}
